@charset "UTF-8";
/*
	Поделючить mixin:
	@import "../vendor/font-face";

	Подключить нестандартные шрифты:
	@include font-face("DR", "../fonts/Damion Regular", 400, normal, ttf);
*/
body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: "R";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/OpenSansRegular.eof") format("eof"), url("../fonts/OpenSansRegular.ttf") format("truetype"), url("../fonts/OpenSansRegular.woff") format("woff"), url("../fonts/OpenSansRegular.woff2") format("woff2"), url("../fonts/OpenSansRegular.svg#R") format("svg");
}

@font-face {
  font-family: "I";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/OpenSansItalic.eof") format("eof"), url("../fonts/OpenSansItalic.ttf") format("truetype"), url("../fonts/OpenSansItalic.woff") format("woff"), url("../fonts/OpenSansItalic.woff2") format("woff2"), url("../fonts/OpenSansItalic.svg#I") format("svg");
}

@font-face {
  font-family: "SB";
  font-style: semi-bold;
  font-weight: 600;
  src: url("../fonts/OpenSansSemiBold.eof") format("eof"), url("../fonts/OpenSansSemiBold.ttf") format("truetype"), url("../fonts/OpenSansSemiBold.woff") format("woff"), url("../fonts/OpenSansSemiBold.woff2") format("woff2"), url("../fonts/OpenSansSemiBold.svg#SB") format("svg");
}

@font-face {
  font-family: "B";
  font-style: bold;
  font-weight: 700;
  src: url("../fonts/OpenSansBold.eof") format("eof"), url("../fonts/OpenSansBold.ttf") format("truetype"), url("../fonts/OpenSansBold.woff") format("woff"), url("../fonts/OpenSansBold.woff2") format("woff2"), url("../fonts/OpenSansBold.svg#B") format("svg");
}

@font-face {
  font-family: "EB";
  font-style: extra-bold;
  font-weight: 800;
  src: url("../fonts/OpenSansExtraBold.eof") format("eof"), url("../fonts/OpenSansExtraBold.ttf") format("truetype"), url("../fonts/OpenSansExtraBold.woff") format("woff"), url("../fonts/OpenSansExtraBold.woff2") format("woff2"), url("../fonts/OpenSansExtraBold.svg#EB") format("svg");
}

html {
  font-size: 100%;
  font-family: 'R';
}

.page h1 {
  font-family: 'EB';
}

.page h2 {
  font-family: 'B';
}

.page h3, .page a {
  font-family: 'SB';
}

.container {
  max-width: 1249px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  padding-left: 26px;
  padding-right: 24px;
}

.description:after {
  content: "";
  display: block;
  width: 132px;
  height: 3px;
  background: #ff2d2d;
}

.description__title {
  text-transform: uppercase;
  font-weight: bold;
  line-height: 1.31;
  opacity: 0.8;
  font-size: 42px;
  letter-spacing: 0.4px;
  color: rgba(0, 0, 0, 0.8);
}

.description__text {
  line-height: 1.75;
  opacity: 0.7;
  color: rgba(0, 0, 0, 0.7);
}

.debug-grid {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug-grid > div {
  height: inherit;
  max-width: 1249px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.debug-grid > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.debug-grid > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(8.33333% - 30px);
  height: inherit;
  outline: #000;
  background: rgba(255, 0, 0, 0.2);
}

.header {
  padding-top: 29px;
  margin-bottom: 102px;
}

.header .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 93px;
}

.header .content .description {
  flex-basis: 47%;
  margin-bottom: 134px;
}

.header .content .description:after {
  content: none;
}

.header .content .description__title {
  font-size: 60px;
  line-height: 1.2;
  letter-spacing: 6px;
  margin-bottom: 17px;
  color: #000000;
}

.header .content .description__text {
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 64px;
}

.header .content .description .btn {
  background-color: #ff2d2d;
  color: #fff;
  padding: 15.5px 64px;
}

.header .content .relation {
  flex-basis: 44%;
  position: relative;
}

.header .content .relation__offset {
  height: 0;
  padding-top: 66.6%;
}

.header .content .relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 10px;
  left: 0;
}

.header .content .relation__video {
  padding-top: 10px;
  position: relative;
  box-shadow: 0 0 30px 5px rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 72.6%;
  min-width: 300px;
  min-height: 228px;
  background-color: #c4c0b1;
}

.header .content .relation__video img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.header .content .relation__video iframe {
  width: 100%;
  height: 100%;
}

.top-menu {
  width: 100%;
}

.top-menu .navbar {
  background-color: #313030;
}

.top-menu .btn-close {
  color: #e0e0e0;
}

.top-menu .btn-close .input {
  width: 100%;
}

.top-menu .btn-close .label {
  width: 100%;
}

.top-menu .menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.top-menu .menu__item:hover {
  background-color: #ff2d2d;
  box-shadow: -0.3px -2px 35px 0 rgba(0, 1, 1, 0.3);
}

.top-menu .menu__link {
  padding: 0 30px;
  width: 100%;
  height: 100%;
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  line-height: 6.4;
  letter-spacing: 0.5px;
  color: #e0e0e0;
}

.amenities {
  margin: auto;
}

.amenities .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.amenities .container:after {
  content: "";
  width: 100%;
  height: 2px;
  background: #ececec;
  margin: auto;
  margin-top: 56px;
}

.amenities .container .description,
.amenities .container .services {
  width: 42%;
}

.amenities .container .description:after {
  content: none;
}

.amenities .container .description__title {
  margin-bottom: 33px;
}

.amenities .container .description__text {
  opacity: 1;
  color: #000000;
  margin-bottom: 60px;
}

.amenities .container .description .btn {
  border: solid 2px #ff0000;
  color: #ff0000;
  padding: 13.5px 58px;
}

.amenities .container .services {
  width: 48.7%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.amenities .container .services .item {
  flex-basis: 47.2%;
  padding-top: 5px;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 19px;
  opacity: 0.7;
}

.amenities .container .services .item__img {
  margin-bottom: 20px;
}

.amenities .container .services .item__title {
  font-size: 24px;
  font-family: 'SB';
  letter-spacing: 0.2px;
  margin-bottom: 9px;
}

.amenities .container .services .item__text {
  line-height: 1.75;
}

.btn {
  border-radius: 5px;
  font-size: 18px;
  letter-spacing: 0.5px;
  line-height: 1.89;
  text-decoration: none;
}

.contact {
  margin-top: 92px;
}

.contact .description,
.contact .contact-form {
  width: 58.5%;
}

.contact .description:after {
  margin-bottom: 75px;
}

.contact .description__title, .contact .description__text {
  text-align: left;
}

.contact .description__title {
  margin-bottom: 34px;
}

.contact .description__text {
  margin-bottom: 49px;
}

.contact .wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact .wrapper .container {
  width: 100%;
}

.contact .wrapper .contact-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact .wrapper .contact-form__input-name, .contact .wrapper .contact-form__input-email, .contact .wrapper .contact-form__message, .contact .wrapper .contact-form__button {
  background: #ccc;
  line-height: 1.89;
  font-size: 18px;
  letter-spacing: .5px;
  color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  border: none;
  padding: 18px 52px;
}

.contact .wrapper .contact-form__input-name:active, .contact .wrapper .contact-form__input-name:focus, .contact .wrapper .contact-form__input-email:active, .contact .wrapper .contact-form__input-email:focus, .contact .wrapper .contact-form__message:active, .contact .wrapper .contact-form__message:focus, .contact .wrapper .contact-form__button:active, .contact .wrapper .contact-form__button:focus {
  outline-color: #ff2d2d;
  box-shadow: inset 0px 0px 10px #ff6060;
}

.contact .wrapper .contact-form__input-name, .contact .wrapper .contact-form__input-email {
  flex-basis: 32%;
}

.contact .wrapper .contact-form__message, .contact .wrapper .contact-form__button {
  flex-basis: 100%;
}

.contact .wrapper .contact-form__button {
  background: #ff2d2d;
  color: #ffffff;
  text-transform: uppercase;
  padding: 19px 0;
}

.contact .wrapper .contact-form__message {
  margin: 43px 0;
  padding: 25px 51px;
}

.contact .contact-info {
  display: flex;
  width: 33.6%;
  background-color: #2d2d2d;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.contact .contact-info__filling {
  opacity: 0.8;
  color: #ffffff;
  line-height: 1.75;
  margin: auto 25px auto 79px;
}

.contact .contact-info__title {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.89;
  letter-spacing: 0.5px;
  margin-bottom: 20px;
}

.contact .contact-info__address {
  margin-bottom: 20px;
}

.contact .contact-info__mail {
  margin-bottom: 20px;
}

.footer {
  margin-top: 110px;
  background-color: #313030;
}

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 126px;
}

.footer .copyright {
  line-height: 1.63;
  color: #b8b8b8;
}

.footer .social-block {
  margin: auto -9px;
}

.footer .social-block__link {
  display: inline-block;
  line-height: 33px;
  width: 33px;
  height: 33px;
  background-color: #ffffff;
  border-radius: 50%;
  margin: auto 9px;
  text-align: center;
  color: #313030;
}

.footer .social-block__link :hover {
  color: #ff2d2d;
}

.header {
  background-color: #f6f2f2;
  width: 100%;
}

.products {
  margin-top: 94px;
}

.products .container {
  flex-direction: column;
}

.products .description {
  width: 43%;
  margin: 0 auto;
}

.products .description:after {
  content: "";
  margin: 0 auto;
  margin-bottom: 80px;
}

.products .description__title, .products .description__text {
  text-align: center;
}

.products .description__title {
  margin-bottom: 17px;
}

.products .description__text {
  margin-bottom: 25px;
}

.products .menu-products .menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 71.7%;
  margin: auto 12.3% 61px 16%;
}

.products .menu-products .menu__link {
  text-decoration: none;
  text-transform: uppercase;
  opacity: 0.7;
  color: rgba(0, 0, 0, 0.7);
  font-size: 22px;
}

.products .menu-products .menu__link:hover {
  color: #ff2d2d;
  opacity: 1;
}

.products .gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.products .gallery__item {
  width: 297.7px;
  height: 290.2px;
  background: #f5f0dd;
  margin-bottom: 20px;
}

.subscribe {
  margin-top: 105px;
  background: #f5f0dd;
  padding: 140px 0;
}

.subscribe-title, .subscribe-text {
  color: #fff;
  text-align: center;
  opacity: 0.8;
  color: rgba(255, 255, 255, 0.8);
}

.subscribe-title {
  font-size: 43px;
  line-height: 0.57;
  letter-spacing: 1.1px;
  margin-bottom: 31px;
}

.subscribe-text {
  font-size: 17px;
  line-height: 1.75;
  margin-bottom: 69px;
}

.subscribe-form {
  width: 757px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.subscribe-form__input-email, .subscribe-form__button {
  border-radius: 5px;
  border: none;
  padding: 28px 28px 28px 35px;
}

.subscribe-form__input-email {
  opacity: 0.7;
  line-height: 1.5;
  letter-spacing: 0.4px;
  color: rgba(44, 44, 44, 0.7);
  flex-grow: 1;
  margin-right: 22px;
}

.subscribe-form__input-email:active, .subscribe-form__input-email:focus {
  outline-color: #ff2d2d;
  box-shadow: inset 0px 0px 10px #ff6060;
}

.subscribe-form__button {
  background: #ff2d2d;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  line-height: 1.89;
  letter-spacing: .5px;
  color: #ffffff;
  padding: 22px 52px;
  cursor: pointer;
}

.teams {
  margin-top: 105px;
}

.teams .description {
  width: 36%;
}

.teams .description:after {
  margin-bottom: 69px;
}

.teams .description__title, .teams .description__text {
  text-align: left;
}

.teams .description__title {
  margin-bottom: 23px;
}

.teams .description__text {
  margin-bottom: 28px;
}

.teams .presentations {
  display: flex;
  justify-content: space-between;
}

.teams .presentations .main-team {
  flex-basis: 25.6%;
  width: 321px;
  height: 458px;
}

.teams .presentations .mark-waugh {
  flex-basis: 68.5%;
  display: flex;
  flex-direction: column;
}

.teams .presentations .mark-waugh__title {
  padding-top: 6px;
  font-size: 30px;
  opacity: 0.8;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.3px;
  color: rgba(0, 0, 0, 0.8);
  margin-bottom: 20px;
}

.teams .presentations .mark-waugh__text {
  width: 74%;
  opacity: 0.7;
  line-height: 1.75;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 36px;
}

.teams .presentations .mark-waugh .social-block {
  margin-bottom: 63px;
}

.teams .presentations .mark-waugh .social-block .menu {
  display: flex;
  flex-direction: row;
  margin: auto -11px;
  opacity: 0.8;
}

.teams .presentations .mark-waugh .social-block .menu__item {
  margin: 0 11px;
}

.teams .presentations .mark-waugh .social-block .menu__link {
  font-style: italic;
  text-decoration: none;
  font-size: 18px;
  line-height: 1.33;
  letter-spacing: 0.9px;
  color: rgba(0, 0, 0, 0.8);
}

.teams .presentations .mark-waugh .social-block .menu__link:hover {
  opacity: 1;
  color: #ff2d2d;
}

.teams .presentations .mark-waugh .gallery {
  display: flex;
  justify-content: space-between;
}

.teams .presentations .mark-waugh .gallery .item {
  position: relative;
  overflow: hidden;
}

.teams .presentations .mark-waugh .gallery .item__popup {
  transition: all .3s;
  position: absolute;
  bottom: -37px;
  left: 0;
  width: 100%;
  height: 37px;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
}

.teams .presentations .mark-waugh .gallery .item__img:hover ~ .item__popup {
  bottom: 5px;
}

.teams .presentations .mark-waugh .gallery .item__link {
  display: block;
  line-height: 2.2;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.2px;
  color: #fff;
}
